<template>
  <div class="baima-container">
    <advert></advert>
    <div class="login-container">
      <language></language>
      <div class="login-inner">
        <el-form class="login-form" autoComplete="on" :model="loginForm" :rules="loginRules" ref="loginForm"
                 label-position="top">
          <el-form-item :label="$t('login.telOrEmail')" prop="username" :class="{'noLabel': !loginForm.username, 'onFocus':loginType.username === 1}">
            <el-input name="username" type="text" v-model="loginForm.username" :placeholder="$t('login.telOrEmail')" @focus="loginType.username = 1" @blur="loginType.username = 0"/>
          </el-form-item>
          <el-form-item :label="$t('login.password')" prop="password" :class="{'noLabel': !loginForm.password, 'onFocus':loginType.password === 1}">
            <el-input name="password" :type="pwdType" @keyup.enter.native="handleLogin" v-model="loginForm.password"
                      :placeholder="$t('login.password')" @focus="loginType.password = 1" @blur="loginType.password = 0"></el-input>
            <span class="show-pwd" @click="showPwd" v-if="loginForm.password">
            <img src="../../images/ico-pass_hidden.png" width="24" alt="" v-if="pwdType">
            <img src="../../images/ico-pass_show.png" width="24" alt="" v-else>
          </span>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" style="width:100%;" :loading="loading" @click.native.prevent="handleLogin"
                       :disabled="!isUsername || !isPassword">
              {{$t('login.signIn')}}
            </el-button>
          </el-form-item>
        </el-form>
        <dl>
          <dt><el-button type="text" @click="goForgotPassword">{{$t('login.forgotPassword')}}</el-button></dt>
          <dd>
            {{$t('login.noAccounts')}}<el-button type="text" @click="goRegister">{{$t('login.register')}}</el-button>
          </dd>
        </dl>
      </div>
      <copy-right></copy-right>
    </div>
  </div>
</template>

<script>
import { isvalidUsername, isvalidUserpassword } from 'common/js/validate'
import Advert from 'base/advert'
import Language from 'base/language'
import copyRight from 'base/copyright'
import { loginByUsername, loginByUsername2 } from 'api/login'
import { setToken } from 'common/js/auth'

import store from '../../store'
import i18n from '../../lang'

export default {
  name: 'login',
  data () {
    const validateUsername = (rule, value, callback) => {
      if (!isvalidUsername(value)) {
        this._data.isUsername = false
        callback(new Error(' '))
      } else {
        this._data.isUsername = true
        callback()
      }
    }
    const validatePass = (rule, value, callback) => {
      if (!isvalidUserpassword(value)) {
        this._data.isPassword = false
        callback(new Error(' '))
      } else {
        this._data.isPassword = true
        callback()
      }
    }
    return {
      loginForm: {
        username: '',
        password: ''
      },
      // 0：无 1：输入中 2：出错：3：完成
      loginType: {
        username: '',
        password: ''
      },
      loginRules: {
        username: [
          {required: true, trigger: 'change', validator: validateUsername},
          {required: true, trigger: 'blur', validator: validateUsername}
        ],
        password: [
          {required: true, trigger: 'change', validator: validatePass},
          {required: true, trigger: 'blur', validator: validatePass}
        ]
      },
      loading: false,
      pwdType: 'password',

      isUsername: '',
      isPassword: '',
      isDisabled: true
    }
  },

  created () {

  },

  mounted() {
    //QT回调方法
    let that = this
    window.setTimeout(() => {

      window.addEventListener('service_ready', function () {
        //QT login
        window.core.qtLogin.connect(function (res) {
          that.loading = false
          // alert(res["ret"])
          that.$store.commit('SET_TOKEN', 'admin')
          // 模拟权限
          setToken('admin')
          that.$router.push({path: '/'})
        })

        // QT language
        window.core.qtTranslate.connect(function (message) {
          i18n.locale = message
          store.commit('SET_LANGUAGE', message)
        })

      })

    }, 40)
    // window.addEventListener('service_ready', function () {
    //
    //   //QT login
    //   window.core.qtLogin.connect(function (res) {
    //     console.log(res)
    //   })
    //
    //   // QT language
    //   window.core.qtTranslate.connect(function (message) {
    //     i18n.locale = message
    //     store.commit('SET_LANGUAGE', message)
    //   })
    //
    // })
  },

  methods: {
    // 显示隐藏右密码
    showPwd () {
      if (this.pwdType === 'password') {
        this.pwdType = ''
      } else {
        this.pwdType = 'password'
      }
    },
    // 处理登录
    handleLogin () {
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          this.loading = true
          let userInfo = Object.assign({}, this.loginForm)
          // 调QT登录接口
          window.core.htmlLogin(userInfo);
            // this.$store.commit('SET_TOKEN', 'admin')
            // // 模拟权限
            // setToken('admin')
            // this.$router.push({path: '/'})
          // }).catch((err) => {
          //   console.log('err', JSON.parse(JSON.stringify(err)))
          //   this.loading = false
          // })
          // this.$store.dispatch('LoginByUsername', {userInfo}).then(() => {
          //   this.loading = false
          //   this.$router.push({path: '/'})
          // }).catch(() => {
          //   this.loading = false
          // })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 跳找回密码
    goForgotPassword() {
      this.$router.push({path: '/forgot-password'})
    },
    // 跳注册
    goRegister() {
      if (goRegisterValue !== 'undefined') {
        console.log(error)
      }
      this.$router.push({path: '/register'})
    }
  },
  components: {
    Advert,
    Language,
    copyRight
  }
}


</script>

<style rel="stylesheet/scss" lang="scss">
@import "common/styles/variable.scss";

/* reset element-ui css */
.login-container {
  .login-form {
    .el-form-item {
      border: none;
      margin-bottom: 8px;
      &:nth-of-type(2) {
        margin-bottom: 36px;
      }
      &:nth-of-type(3) {
        margin-bottom: 2px;
      }
      &.noLabel label {
        visibility: hidden;
      }
      & label::before {
        display: none;
      }
      .el-input {
        display: inline-block;
        height: 36px;
        input {
          background: transparent;
          border: 0px;
          -webkit-appearance: none;
          border-radius: 0px;
          padding: 9px 5px 9px 0;
          color: $gray_dark;
          height: 36px;
          font-size: 18px;
          border-bottom: 1px solid $gray_line;
          &::-webkit-input-placeholder {
            color: $gray_placeholder;
          }
        }
      }
      .el-input__inner:focus {
        border-bottom: 1px solid $gray_line_on;
      }
      label {
        color: $gray_placeholder;
        font-size: 12px;
        line-height: 12px;
        padding: 8px 0 0 0;
      }
      &.is-error {
        label {
          visibility: visible;
          color: $red;
        }
        .el-input__inner {
          border-bottom: 1px solid $red;
        }
        .el-input__inner:focus {
          border-bottom: 1px solid $gray_line_on;
        }
      }
      &.onFocus {
         label {
           visibility: visible;
           color: $gray_line_on;
         }
       }
      .el-button {
        padding: 15px 20px;
        font-size: 18px;
        background-color: $gray_line_on;
        border: none;
        border-radius: 4px;
      }
      .el-button--medium {
        font-size: 18px;
        color: $white;
      }
      .is-disabled {
        background: $gray_line;
        color: $gray_light;
      }
    }
  }
  .login-inner {
    .el-button--medium {
      font-size: 12px;
      color: $blue;
    }
  }
}

</style>

<style rel="stylesheet/scss" lang="scss" scoped>
@import "common/styles/variable.scss";
.baima-container {
  position: fixed;
  height: 100%;
  width: 100%;
  display: flex;
  .login-container {
    flex: 1;
    position: relative;
    .login-inner {
      position: absolute;
      left: 50%;
      right: 0;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 366px;
      margin: 0 auto;
      .show-pwd {
        position: absolute;
        right: 0;
        top: 10px;
        height: 24px;
        cursor: pointer;
        user-select: none;
      }
      dl {
        display: flex;
        justify-content: space-between;
        font-size: 12px;
        dt {
          flex: 1;
        }
        dd {
          flex: 1;
          color: $gray_light;
          text-align: right;
        }
      }
    }
  }
}
</style>



